import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { Light } from './light';
import { Temperature } from './temperature';
import { Humidity } from './humidity';
import { Rooms } from './rooms';
import { Message } from './message';
import * as firebase from 'firebase/app';
import { Subscription } from 'rxjs';
import { MqttService } from 'ngx-mqtt';

@Injectable()
export class DataService {
  public roomsList = new Rooms().getRoomList();
  private temperaturesList: AngularFireList<any>;
  private lightsList: AngularFireList<any>;
  private humidityList: AngularFireList<any>;
  private subscription: Subscription;

  constructor(
    private afDb: AngularFireDatabase,
    private mqttService: MqttService
  ) {
    this.temperaturesList = this.afDb.list('temperature');
    this.humidityList = this.afDb.list('humidity');
    this.lightsList = this.afDb.list('lights');
  }

  getTemperature() {
    this.temperaturesList = this.afDb.list('temperatures');
    return this.temperaturesList;
  }

  getHumidity() {
    this.humidityList = this.afDb.list('humidity');
    return this.humidityList;
  }

  getLights() {
    this.lightsList = this.afDb.list('lights');
    return this.lightsList;
  }

  getTemperatures(conversation) {
    firebase.database().ref('temperature').once('value').then(item => {
      const tempList = [];
      item.forEach(element => {
        const temp = element.val();
        tempList.push(temp as Temperature);
      });
      const output = 'The temperature of ' + this.roomsList[0] + ' is ' + tempList[0].value + '. '
        + 'The temperature of ' + this.roomsList[1] + ' is ' + tempList[1].value + '. '
        + 'The temperature of ' + this.roomsList[2] + ' is ' + tempList[2].value + '. '
        + 'The temperature of ' + this.roomsList[3] + ' is ' + tempList[3].value + '. '
        + 'The temperature of ' + this.roomsList[4] + ' is ' + tempList[4].value + '. '
        + 'The temperature of ' + this.roomsList[5] + ' is ' + tempList[5].value + '. ';
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  getHumidities(conversation) {
    firebase.database().ref('humidity').once('value').then(item => {
      const humList = [];
      item.forEach(element => {
        const hum = element.val();
        humList.push(hum as Humidity);
      });
      const output = 'The humidity of ' + this.roomsList[0] + ' is ' + humList[0].value + '. '
        + 'The humidity of ' + this.roomsList[1] + ' is ' + humList[1].value + '. '
        + 'The humidity of ' + this.roomsList[2] + ' is ' + humList[2].value + '. '
        + 'The humidity of ' + this.roomsList[3] + ' is ' + humList[3].value + '. '
        + 'The humidity of ' + this.roomsList[4] + ' is ' + humList[4].value + '. '
        + 'The humidity of ' + this.roomsList[5] + ' is ' + humList[5].value + '. ';
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  getLightsStates(conversation) {
    firebase.database().ref('lights').once('value').then(item => {
      const liList = [];
      item.forEach(element => {
        const li = element.val();
        if ((li as Light).state === 1) {
          liList.push('on');
        } else {
          liList.push('off');
        }
      });
      const output = 'The light of ' + this.roomsList[0] + ' is ' + liList[0] + '. '
        + 'The light of ' + this.roomsList[1] + ' is ' + liList[1] + '. '
        + 'The light of ' + this.roomsList[2] + ' is ' + liList[2] + '. '
        + 'The light of ' + this.roomsList[3] + ' is ' + liList[3] + '. '
        + 'The light of ' + this.roomsList[4] + ' is ' + liList[4] + '. '
        + 'The light of ' + this.roomsList[5] + ' is ' + liList[5] + '. ';
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  getTemperatureElement(room, input, conversation) {
    firebase.database().ref('temperature/' + room).once('value').then(item => {
      const temperature = (item.val() as Temperature);
      const replace = '$temperature$';
      const output = input.replace(replace, temperature.value);
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  getHumidityElement(room, input, conversation) {
    firebase.database().ref('humidity/' + room).once('value').then(item => {
      const humidity = (item.val() as Humidity);
      const replace = '$humidity$';
      const output = input.replace(replace, humidity.value);
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  getLightSate(room, input, conversation) {
    firebase.database().ref('lights/' + room).once('value').then(item => {
      const light = (item.val() as Light);
      let state;
      if (light.state === 1) {
        state = 'on';
      } else {
        state = 'off';
      }
      const replace = '$light-state$';
      const output = input.replace(replace, state);
      const botMessage = new Message(output, 'bot');
      conversation.next([botMessage]);
    });
  }

  setLightSate(room, input, conversation) {
    let state;
    let message;
    let replace;
    if (input.includes('$turn-on$')) {
      state = 1;
      message = 'on';
      replace = '$turn-on$';
    } else {
      state = 0;
      message = 'off';
      replace = '$turn-off$';
    }
    this.subscription = this.mqttService.publish('lightState', room[4] + state).subscribe(() => { });
    this.lightsList.update(room, {
      state: state
    });
    const output = input.replace(replace, message);
    const botMessage = new Message(output, 'bot');
    conversation.next([botMessage]);
  }

  setAllLightSate(input, conversation) {
    let state;
    let message;
    let replace;
    if (input.includes('$turn-on-all$')) {
      state = 1;
      message = 'on';
      replace = '$turn-on-all$';
    } else {
      state = 0;
      message = 'off';
      replace = '$turn-off-all$';
    }
    for (let i = 1; i <= 6; i++) {
      this.subscription = this.mqttService.publish('lightState', i + '' + state).subscribe(() => { });
      this.lightsList.update('room' + i, {
        state: state
      });
    }
    const output = input.replace(replace, message);
    const botMessage = new Message(output, 'bot');
    conversation.next([botMessage]);
  }
}
