import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { DataService } from './data.service';
import { Message } from './message';
import { Rooms } from './rooms';
import { JSONData } from './JSONData';

@Injectable()
export class ChatService {
  public roomsList = new Rooms().getRoomList();
  readonly baseURL = 'https://api.dialogflow.com/v1/query';
  readonly token = environment.developerAccessToken;
  readonly sessioId = 'gEAMpOd50625lPt9IK4u0+IZM3umA5totT1phaHZYAV\nxiQjIQXdzH8IrVJin+e+tJ1MAJLkcMihIo25';
  conversation = new BehaviorSubject<Message[]>([]);

  constructor(
    private http: HttpClient,
    private data: DataService
  ) {
    const botMessage = new Message('Hi. I am your IoT agent. Tell me what you want to do.', 'bot');
    this.update(botMessage);
  }

  converse(msg: string) {
    const userMessage = new Message(msg, 'user');
    this.update(userMessage);
    this.getRequest(msg);
  }

  update(msg: Message) {
    this.conversation.next([msg]);
  }

  getRequest(msg) {
    const headers = new HttpHeaders()
      .set('Authorization', 'Bearer ' + this.token);

    const params = new HttpParams()
      .set('v', '20170712')
      .set('lang', 'en')
      .set('query', msg)
      .set('sessionId', this.sessioId)
      .set('timezone', 'Europe/Kaliningrad');

    const httpOptions = {
      headers: headers,
      params: params
    };

    this.http.get(this.baseURL, httpOptions)
      .subscribe(
        data => {
          const inputText = (data as JSONData).result.fulfillment.speech;
          this.caseHandling(inputText);
        },
        error => {
          const botMessage = new Message('Error! Something went terribly wrong. Read about it. Try again later.', 'bot');
          console.log('This is for the developer: ' + error);
          this.update(botMessage);
        }
      );
  }

  caseHandling(input) {
    if (input.includes('$temperature$')) {
      this.temperatureMeasuring(input);
    } else if (input.includes('$humidity$')) {
      this.humidityMeasuring(input);
    } else if (input.includes('$light$')) {
      this.lightHandling(input);
    } else if (input.includes('$light-state$')) {
      this.lightMeasuring(input);
    } else if (input === '') {
      const botMessage = new Message('Something went wrong. Try again.', 'bot');
      this.update(botMessage);
    } else {
      const botMessage = new Message(input, 'bot');
      this.update(botMessage);
    }
  }

  temperatureMeasuring(input) {
    if (input.includes('$room')) {
      let roomNumber = '';
      for (let i = input.indexOf('$room') + 5; i < input.length; i++) {
        if (input[i] !== '$') {
          roomNumber = roomNumber + input[i];
        } else {
          break;
        }
      }
      const roomName = this.roomsList[Number(roomNumber) - 1];
      const replace = '$room' + roomNumber + '$';
      const output = input.replace(replace, roomName);
      this.data.getTemperatureElement('room' + roomNumber, output, this.conversation);
    } else {
      this.data.getTemperatures(this.conversation);
    }
  }

  humidityMeasuring(input) {
    if (input.includes('$room')) {
      let roomNumber = '';
      for (let i = input.indexOf('$room') + 5; i < input.length; i++) {
        if (input[i] !== '$') {
          roomNumber = roomNumber + input[i];
        } else {
          break;
        }
      }
      const roomName = this.roomsList[Number(roomNumber) - 1];
      const replace = '$room' + roomNumber + '$';
      const output = input.replace(replace, roomName);
      this.data.getHumidityElement('room' + roomNumber, output, this.conversation);
    } else {
      this.data.getHumidities(this.conversation);
    }
  }

  lightMeasuring(input) {
    if (input.includes('$room')) {
      let roomNumber = '';
      for (let i = input.indexOf('$room') + 5; i < input.length; i++) {
        if (input[i] !== '$') {
          roomNumber = roomNumber + input[i];
        } else {
          break;
        }
      }
      const roomName = this.roomsList[Number(roomNumber) - 1];
      const replace = '$room' + roomNumber + '$';
      const output = input.replace(replace, roomName);
      this.data.getLightSate('room' + roomNumber, output, this.conversation);
    } else {
      this.data.getLightsStates(this.conversation);
    }
  }

  lightHandling(input) {
    const output = input.replace('$light$', '');
    if (input.includes('$turn-on$') || input.includes('$turn-off$')) {
      let roomNumber = '';
      for (let i = input.indexOf('$room') + 5; i < input.length; i++) {
        if (input[i] !== '$') {
          roomNumber = roomNumber + input[i];
        } else {
          break;
        }
      }
      const roomName = this.roomsList[Number(roomNumber) - 1];
      const replace = '$room' + roomNumber + '$';
      const out = output.replace(replace, roomName);
      this.data.setLightSate('room' + roomNumber, out, this.conversation);
    } else if (input.includes('$turn-on-all$') || input.includes('$turn-off-all$')) {
      const replace = '$all-rooms$';
      const out = output.replace(replace, 'house');
      this.data.setAllLightSate(out, this.conversation);
    }
  }
}
