
import { scan } from 'rxjs/operators';
import { Component, OnInit } from '@angular/core';
import { ChatService } from '../services/chat.service';
import { Observable } from 'rxjs';
import { Message } from '../services/message';

@Component({
  selector: 'app-chat-dialog',
  templateUrl: './chat-dialog.component.html',
  styleUrls: ['./chat-dialog.component.css']
})
export class ChatDialogComponent implements OnInit {

  public messages: Observable<Message[]>;
  public formValue: string;

  constructor(
    private chat: ChatService
  ) {
    this.formValue = '';
    this.messages = this.chat.conversation.asObservable().pipe(scan((acc, val) => acc.concat(val)));
  }

  ngOnInit() {
    const botMessage = new Message('Hi. I am your IoT agent. Tell me what you want to do.', 'bot');
    this.chat.conversation.next([botMessage]);
  }

  sendMessage() {
    if (this.formValue !== '') {
      this.chat.converse(this.formValue);
      this.formValue = '';
    }
  }
}
