import { Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AngularFireObject } from '@angular/fire/database';
import { User } from '../core/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public loginForm: FormGroup;
  public errorMessage = '';
  public user: User = new User;
  public userReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private authService: AuthService,
    private router: Router,
    private formBuilder: FormBuilder
  ) {
    this.createForm();
  }

  createForm() {
    this.loginForm = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  tryLogin(formValues) {
    this.authService.doLogin(formValues)
      .then(result => {
        this.router.navigate(['/agent']);
      }, error => {
        if (error.message.includes('signInWithEmailAndPassword failed: ')) {
          error.message = error.message.replace('signInWithEmailAndPassword failed: ', '');
        }
        this.errorMessage = '*' + error.message;
        this.loginForm.reset();
      });
  }
}
