import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { Light } from '../chat/services/light';
import { Temperature } from '../chat/services/temperature';
import { Humidity } from '../chat/services/humidity';
import { Rooms } from '../chat/services/rooms';
import { Subscription } from 'rxjs';
import { MqttService } from 'ngx-mqtt';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  public roomsList = new Rooms().getRoomList();
  private lightsList: AngularFireList<any>;
  private temperaturesList: AngularFireList<any>;
  private humidityList: AngularFireList<any>;
  public light: Light[];
  public temperature: Temperature[];
  public humidity: Humidity[];
  private subscription: Subscription;

  constructor(
    private afDb: AngularFireDatabase,
    private mqttService: MqttService
  ) {
    this.init();
  }

  ngOnInit() {
    this.getLightStates();
    this.getTemperature();
    this.getHumidity();
  }

  init() {
    this.light = [];
    this.temperature = [];
    this.humidity = [];
    const l = new Light();
    const t = new Temperature();
    const h = new Humidity();
    for (let i = 0; i < 6; i++) {
      l.state = 0;
      t.value = 0;
      h.value = 0;
      this.light.push(l);
      this.temperature.push(t);
      this.humidity.push(h);
    }
  }

  getLightStates() {
    this.lightsList = this.afDb.list('lights');
    this.lightsList.snapshotChanges().subscribe(item => {
      this.light = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['lid'] = element.key;
        this.light.push(y as Light);
      });
    });
  }

  getTemperature() {
    this.temperaturesList = this.afDb.list('temperature');
    this.temperaturesList.snapshotChanges().subscribe(item => {
      this.temperature = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['tid'] = element.key;
        this.temperature.push(y as Temperature);
      });
    });
  }

  getHumidity() {
    this.humidityList = this.afDb.list('humidity');
    this.humidityList.snapshotChanges().subscribe(item => {
      this.humidity = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['hid'] = element.key;
        this.humidity.push(y as Humidity);
      });
    });
  }

  changeState(light, index) {
    if (light.state === 1) {
      this.subscription = this.mqttService.publish('lightState', index + 1 + '0').subscribe(() => { });
      this.lightsList.update(light.lid, {
        state: 0
      });
    } else {
      this.subscription = this.mqttService.publish('lightState', index + 1 + '1').subscribe(() => { });
      this.lightsList.update(light.lid, {
        state: 1
      });
    }
  }
}
